import { createRouter, createWebHistory, RouteRecordRaw } from "vue-router"

const routes: Array<RouteRecordRaw> = [
  {
    path: "/",
    name: "home",
    component: () => import("@/components/pages/Home.vue"),
    meta: { title: "HOME" }
  },
  {
    path: "/live",
    name: "live",
    component: () => import("@/components/pages/Live.vue"),
    meta: { title: "LIVE" }
  },
  {
    path: "/about",
    name: "about",
    component: () => import("@/components/pages/About.vue"),
    meta: { title: "ABOUT" }
  },
  {
    path: "/music",
    name: "music",
    component: () => import("@/components/pages/Music.vue"),
    meta: { title: "MUSIC" }
  },
  {
    path: "/do-your-best-as-death-maybe-upon",
    name: "shinuki",
    component: () => import("@/components/pages/Shinuki.vue"),
    meta: { title: "死ぬ気でかかって来い！" }
  },
]

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes
})

router.afterEach((to) => {
  document.title = to.meta.title ? "sly cat girl | " + to.meta.title : "sly cat girl"
})

export default router
